<template>
  <section class='viewer'>
    <img class='viewer-img' :src='`/img/game/${data.skillpic}.jpg`' />
    <div class='viewer-description'>
      <h1>{{data.name}}</h1>
      <p>{{data.description}}</p>
      <p>cost: 
        <span v-for="(c, k) in costs" :key="k" :class="`viewer-energy ${c}`"></span>
      </p>
    </div>
  </section>
</template>

<script>
import components from "./script/components";
import computed from "./script/computed";
import methods from "./script/methods";
import watchers from "./script/watchers";

export default {
  data() {
    return {
      data: {},
    };
  },
  components,
  computed,
  methods,
  watchers,
  created() {
    this.$eventBus.$on("selected-a-skill", (skill) => {
      this.data = skill;
    });
  },
};
</script>

